
.scroll-top {
  background-color: #F55536;
  position: fixed;
  right: 20px;
  bottom: 70px;
  size: 50px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  cursor: pointer;
  opacity: 1;
  -webkit-transition: .3s;
  transition: .3s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.scroll-top.hoverable {
    opacity: 0.4;
}
.scroll-top.hover {
    opacity: 1;
}
.back-top-fade-enter,
.back-top-fade-leave-active {
  -webkit-transform: translateY(-30px);
      -ms-transform: translateY(-30px);
          transform: translateY(-30px);
  opacity: 0;
}



